/* global ymaps */

export default () => {
  const contactsPageMap = document.querySelector(`.js-contacts-map`);
  const addressCards = document.querySelectorAll(`.js-map-card`);

  if (!contactsPageMap) {
    return;
  }

  const icon = {
    defaultSize: [40, 40],
    activeSize: [46, 46],
  };
  const locations = [
    {
      title: `Центральный офис`,
      position: [55.846978, 37.657678],
      defaultUrl: `img/contacts-map/marker1.png`,
      activeUrl: `img/contacts-map/marker1-active.png`,
    },
    {
      title: `Офис в городе Пушкино`,
      position: [56.011684, 37.853307],
      defaultUrl: `img/contacts-map/marker2.png`,
      activeUrl: `img/contacts-map/marker2-active.png`,
    },
    {
      title: `Офис в городе Королёв`,
      position: [55.941712, 37.870026],
      defaultUrl: `img/contacts-map/marker3.png`,
      activeUrl: `img/contacts-map/marker3-active.png`,
    },
  ];

  function init() {
    const map = new ymaps.Map(`main-page-map`, {
      center: [55.941712, 37.870026],
      zoom: 10,
      controls: [],
    });

    function calcIconImageOffset(iconSize) {
      return [-Math.abs(iconSize[0] / 2), -Math.abs(iconSize[1] / 2)];
    }

    const markers = locations.map((element) => new ymaps.Placemark(element.position, {}, {
      iconLayout: `default#image`,
      iconImageHref: element.defaultUrl,
      iconImageSize: icon.defaultSize,
      iconImageOffset: calcIconImageOffset(icon.defaultSize),
      zIndex: 1,
    }));

    markers.forEach((marker) => {
      map.geoObjects.add(marker);
    });

    const addMarker = (index) => {
      markers.forEach((marker, i) => {
        marker.options.set(`iconImageHref`, locations[i].defaultUrl);
        marker.options.set(`iconImageSize`, icon.defaultSize);
        marker.options.set(`iconImageOffset`, calcIconImageOffset(icon.defaultSize));
        markers[index].options.set(`zIndex`, 1);
      });
      markers[index].options.set(`iconImageHref`, locations[index].activeUrl);
      markers[index].options.set(`iconImageSize`, icon.activeSize);
      markers[index].options.set(`iconImageOffset`, calcIconImageOffset(icon.activeSize));
      markers[index].options.set(`zIndex`, 10);
      map.panTo(markers[index].geometry._coordinates);
    };

    addressCards.forEach((addressCard) => {
      addressCard.addEventListener(`click`, (evt) => {
        if (evt.target.tagName === 'H3' || evt.target.tagName === 'IMG') {
          addressCards.forEach((card) => {
            card.classList.remove(`active`);
          });
          addressCard.classList.add(`active`);
          addMarker(addressCard.getAttribute(`data-index`));
        } else if (evt.target.closest('button')){
          evt.preventDefault();
        }
      });
    });
  }

  function yandexMaps() {
    ymaps.ready(init);
  }

  yandexMaps();
};
