import Swiper from '../lib/swiper.min.js';

export default () => {
  const news = document.querySelector(`.js-news`);

  if (!news) {
    return;
  }

  const newsSlider = news.querySelector(`.swiper-container`);
  let itemSlider;

  const breakpoint = window.matchMedia(`(min-width: 768px)`);

  function enableSwiper() {
    itemSlider = new Swiper(newsSlider, {
      slidesPerView: `auto`,
      watchOverflow: true,
      watchSlidesVisibility: true,
      slidesOffsetAfter: 0,
      spaceBetween: 20,
    });
  }

  function breakpointChecker() {
    if (breakpoint.matches === true) {
      if (itemSlider !== undefined) {
        itemSlider.destroy(true, true);
      }
    } else {
      enableSwiper();
    }
  }

  breakpoint.addListener(breakpointChecker);

  breakpointChecker();
};
