import Swiper from '../lib/swiper.min.js';

export default () => {
  const mapToggle = document.querySelector(`.js-map-toggle`);
  const list = document.querySelector(`.js-main-projects-list`);

  if (!list) {
    return;
  }

  const projectCard = list.querySelectorAll(`li`);

  const mainPageSlider = list.querySelector(`.swiper1`);
  const firstSlides = list.querySelectorAll('.swiper2 .swiper-slide:first-child');
  const firstProject = list.querySelector('li:first-child');

  let itemSlider;

  let sliderArray = [];
  let swiperContainer;
  let projectSlider;

  const breakpoint = window.matchMedia(`(min-width: 1200px)`);
  const breakpointMap = window.matchMedia(`(max-width: auto)`);

  function enableSwiper() {
    itemSlider = new Swiper(mainPageSlider, {
      observer: true,
      observeParents: true,
      slidesPerView: `auto`,
      watchOverflow: true,
      watchSlidesVisibility: true,
      slidesOffsetAfter: 0,
      spaceBetween: 20,
      direction: 'vertical',
      mousewheel: true,
      scrollbar: {
        el: `.swiper-scrollbar`,
        draggable: true,
      },
      breakpoints: {
        320: {
          direction: 'horizontal',
        },
        1200: {
          direction: 'vertical',
          slidesPerView: `3.5`,
        },
      },
    });
  }

  function enableProjectSwiper(item) {
    swiperContainer = item.querySelector(`.swiper2`);
    projectSlider = new Swiper(swiperContainer, {
      loop: false,
      autoplay: {
        delay: 1500,
        stopOnLastSlide: true,
      },
      speed: 500,
      effect: 'fade',
    });

    projectSlider.autoplay.stop();
    projectSlider.slideTo(0);

    return projectSlider;
  }


  function addClass(item, targetClass) {
    if (!item.classList.contains(targetClass)) {
      item.classList.add(targetClass);
    }
  }

  function removeClass(item, targetClass) {
    if (item.classList.contains(targetClass)) {
      item.classList.remove(targetClass);
    }
  }

  function breakpointChecker() {
    if (breakpointMap.matches === true) {
      if (itemSlider !== undefined) {
        itemSlider.destroy(true, true);
      }
      if (!list.classList.contains(`main-page__projects-list--map`)) {
        sliderArray.push(enableProjectSwiper(firstProject));
      } else {
        if (sliderArray.length) {
          sliderArray.forEach((slider) => {
            slider.destroy(true, true);
          });
          sliderArray = [];
        }
      }
    } else {
      if (list.classList.contains(`main-page__projects-list--map`)) {
        enableSwiper();

        sliderArray.forEach((slider) => {
          slider.destroy(true, true);
        });
        sliderArray = [];

      } else {
        sliderArray.push(enableProjectSwiper(firstProject));
      }
    }
  }

  function onMapToggleClick() {
    mapToggle.classList.toggle(`active`);
    list.classList.toggle(`main-page__projects-list--map`);

    if (list.classList.contains(`main-page__projects-list--map`)) {
      breakpointChecker();
    } else {
      if (itemSlider !== undefined) {
        itemSlider.destroy(true, true);
      }
      if (!sliderArray.length) {
        sliderArray.push(enableProjectSwiper(firstProject));
      }

    }
  }

  function onCardClick(evt) {
    if (list.classList.contains(`main-page__projects-list--map`)) {
      return;
    }

    const {target} = evt;
    const item = target.querySelector(`.js-project-link`) || target.parentNode.querySelector(`.js-project-link`) || target.parentNode.parentNode.querySelector(`.js-project-link`) || target.parentNode.parentNode.parentNode.querySelector(`.js-project-link`);
    item.click();
  }

  function defineDelay(slide, time) {
    slide.setAttribute('data-swiper-autoplay', time);
  }

  firstSlides.forEach((slide) => {
    defineDelay(slide, '50');
  });

  mapToggle.addEventListener(`click`, onMapToggleClick);

  breakpoint.addListener(breakpointChecker);

  breakpointChecker();

  projectCard.forEach((card) => {
    card.addEventListener(`click`, onCardClick);
  });


  let mySwiper = firstProject.querySelector('.swiper2');
  firstProject.addEventListener('mouseenter', function () {
    if (breakpoint.matches === true && !list.classList.contains(`main-page__projects-list--map`)) {
      addClass(firstProject, 'project-card--active');
      mySwiper.swiper.autoplay.start();
    }
  });
  firstProject.addEventListener('mouseleave', function () {
    if (breakpoint.matches === true && !list.classList.contains(`main-page__projects-list--map`)) {
      removeClass(firstProject, 'project-card--active');

      mySwiper.swiper.autoplay.stop();
      mySwiper.swiper.slideTo(0);
    }
  });

  function onProjectSliderClick(evt) {
    evt.stopPropagation();
    if (!breakpoint.matches && !list.classList.contains(`main-page__projects-list--map`)) {
      addClass(firstProject, 'project-card--active');
      mySwiper.swiper.autoplay.start();
      setTimeout(function() {
        removeClass(firstProject, 'project-card--active');
        mySwiper.swiper.slideTo(0);
      }, 6300);
    }
  }

  let button = firstProject.querySelector('.project-card__button');

  button.addEventListener('click', onProjectSliderClick, false);
};
