import Swiper from '../lib/swiper.min.js';

export default () => {
  const choiceList = document.querySelector(`.choice`);

  if (!choiceList) {
    return null;
  }

  const choiceSlider = choiceList.querySelector(`.swiper-container`);

  return new Swiper(choiceSlider, {
    slidesPerView: `auto`,
    watchOverflow: true,
    watchSlidesVisibility: true,
    watchSlidesProgress: true,
    threshold: 5,
    slidesOffsetAfter: 0,
  });
};
