export default () => {
  const header = document.querySelector(`.js-header`);
  const toggle = header.querySelector(`.js-menu-toggle`);
  const layout = document.querySelector(`.js-layout`);

  if (!header || !toggle) {
    return;
  }

  function onMenuToggleClick() {
    header.classList.toggle(`active`);
    layout.classList.toggle(`menu-opened`);
  }

  toggle.addEventListener(`click`, onMenuToggleClick);
};
