import Swiper from '../lib/swiper.min.js';

export default () => {
  const anchorLinks = document.querySelectorAll(`.js-anchor-link`);
  const header = document.querySelector(`.js-header`);
  const anchorHeader = document.querySelector(`.js-anchor-header`);
  const targets = document.querySelectorAll(`.js-section[data-name]`);
  let isScrolling = false;

  if (!anchorHeader || !header) {
    return;
  }

  let headerHeight = header.clientHeight + anchorHeader.clientHeight;
  anchorLinks[0].classList.add(`active`);

  const anchorSlider = new Swiper(anchorHeader, {
    slidesPerView: `auto`,
    watchOverflow: true,
    watchSlidesVisibility: true,
    watchSlidesProgress: true,
    threshold: 5,
    slidesOffsetAfter: 0,
    speed: 50,
  });

  function getTop(elem) {
    const box = elem.getBoundingClientRect();
    const boxTop = box.top + pageYOffset;
    return boxTop;
  }

  function onLinkClick(evt) {
    const {target} = evt;
    const item = target.closest(`.js-anchor-link`);

    if (item) {
      evt.preventDefault();

      const targetName = item.getAttribute(`data-target`);

      for (let t = 0; t < targets.length; t++) {
        if (targets[t].getAttribute(`data-name`) === targetName) {
          isScrolling = true;

          targets[t].style.top = (headerHeight * (-1)) + `px`;

          targets[t].scrollIntoView({behavior: `smooth`});

          if (window.innerWidth <= 1200) {
            let active = anchorHeader.querySelector(`.active`);
            active.classList.remove(`active`);
            item.classList.add(`active`);
            anchorSlider.slideTo(t);

            setTimeout(() => {
              isScrolling = false;
            }, 3000);
          } else {
            isScrolling = false;
          }
        }
      }
    }
  }

  function onWindowResize() {
    headerHeight = header.clientHeight + anchorHeader.clientHeight;
    targets.forEach((target) => {
      target.style.top = (-1 * headerHeight) + `px`;
    });
  }

  onWindowResize();

  window.addEventListener(`resize`, onWindowResize);

  anchorHeader.addEventListener(`click`, onLinkClick);

  window.onscroll = () => {
    targets.forEach((target, index) => {
      const targetTop = getTop(target);
      const targetName = target.getAttribute(`data-name`);
      const targetLink = anchorHeader.querySelector(`[data-target =` + targetName + `]`);

      if (!isScrolling) {
        if (document.body.scrollTop > targetTop - 20 || document.documentElement.scrollTop > targetTop - 20) {
          let active = anchorHeader.querySelector(`.active`);
          active.classList.remove(`active`);
          targetLink.classList.add(`active`);
          anchorSlider.slideTo(index);
        }
      }
    });
  };
};
