import Swiper from '../lib/swiper.min.js';

export default () => {
  const contacts = document.querySelector(`.js-contacts-slider`);

  if (!contacts) {
    return;
  }

  const projectsSlider = contacts.querySelector(`.swiper-container`);
  let itemSlider;

  const breakpoint = window.matchMedia(`(min-width: 1200px)`);

  function enableSwiper() {
    itemSlider = new Swiper(projectsSlider, {
      observer: true,
      observeParents: true,
      slidesPerView: `auto`,
      watchOverflow: true,
      watchSlidesVisibility: true,
      slidesOffsetAfter: 0,
      spaceBetween: 20,
    });
  }

  function breakpointChecker() {
    if (breakpoint.matches === true) {
      if (itemSlider !== undefined) {
        itemSlider.destroy(true, true);
      }
    } else {
      enableSwiper();
    }
  }

  breakpoint.addListener(breakpointChecker);

  breakpointChecker();
};
