/* global ymaps */

export default () => {
  const mainPageMap = document.querySelector(`.js-main-projects-map`);
  const addressCards = document.querySelectorAll(`.js-map-card`);

  if (!mainPageMap) {
    return;
  }

  const icon = {
    defaultSize: [40, 40],
    activeSize: [46, 46],
  };
  const locations = [
    {
      title: `ЖК Ривер Парк в г. Королёв`,
      position: [55.941422, 37.869812],
      defaultUrl: `img/main-map/marker1.png`,
      activeUrl: `img/main-map/marker1-active.png`,
    },
    {
      title: `ЖК 31 Квартал в г. Пушкино`,
      position: [55.999512, 37.869755],
      defaultUrl: `img/main-map/marker2.png`,
      activeUrl: `img/main-map/marker2-active.png`,
    },
    {
      title: `Жилой дом Правда-4 в п. Правдинском`,
      position: [56.063412, 37.850101],
      defaultUrl: `img/main-map/marker3.png`,
      activeUrl: `img/main-map/marker3-active.png`,
    },
    {
      title: `ЖК на Тургенева 13 в г. Пушкино`,
      position: [56.012642, 37.852726],
      defaultUrl: `img/main-map/marker4.png`,
      activeUrl: `img/main-map/marker4-active.png`,
    },
    {
      title: `Жилой дом на Писаревской 5 в г. Пушкино`,
      position: [56.008419, 37.842583],
      defaultUrl: `img/main-map/marker5.png`,
      activeUrl: `img/main-map/marker5-active.png`,
    },
  ];

  function init() {
    const map = new ymaps.Map(`main-page-map`, {
      center: [56.008419, 37.842583],
      zoom: 10,
      controls: [],
    });

    function calcIconImageOffset(iconSize) {
      return [-Math.abs(iconSize[0] / 2), -Math.abs(iconSize[1] / 2)];
    }

    const markers = locations.map((element) => new ymaps.Placemark(element.position, {}, {
      iconLayout: `default#image`,
      iconImageHref: element.defaultUrl,
      iconImageSize: icon.defaultSize,
      iconImageOffset: calcIconImageOffset(icon.defaultSize),
      zIndex: 1,
    }));

    markers.forEach((marker, index) => {
      map.geoObjects.add(marker);
      marker.index = index;
      marker.events.add('click', function (evt) {
        const target = evt.get('target')
        const { index } = target;
        const item = addressCards[index];

        if (item) {
          addressCards.forEach((card) => {
            card.classList.remove(`active`);
          });
          item.classList.add(`active`);
          addMarker(item.getAttribute(`data-index`));
        }
      });
    });

    const addMarker = (index) => {
      markers.forEach((marker, i) => {
        marker.options.set(`iconImageHref`, locations[i].defaultUrl);
        marker.options.set(`iconImageSize`, icon.defaultSize);
        marker.options.set(`iconImageOffset`, calcIconImageOffset(icon.defaultSize));
        markers[index].options.set(`zIndex`, 1);
      });
      markers[index].options.set(`iconImageHref`, locations[index].activeUrl);
      markers[index].options.set(`iconImageSize`, icon.activeSize);
      markers[index].options.set(`iconImageOffset`, calcIconImageOffset(icon.activeSize));
      markers[index].options.set(`zIndex`, 10);
      map.panTo(markers[index].geometry._coordinates);
    };

    addressCards.forEach((addressCard) => {
      addressCard.addEventListener(`click`, (evt) => {
        const {target} = evt;
        const item = target.closest(`button`);

        if (item) {
          addressCards.forEach((card) => {
            card.classList.remove(`active`);
          });
          addressCard.classList.add(`active`);
          addMarker(addressCard.getAttribute(`data-index`));
        }
      });
    });
  }

  function yandexMaps() {
    ymaps.ready(init);
  }

  yandexMaps();
};
