import Swiper from '../lib/swiper.min.js';
export default () => {
  const sliderContainerList = document.querySelectorAll(`.js-slider-container`);

  if (!sliderContainerList.length) {
    return;
  }

  const layout = document.querySelector(`.js-layout`);

  // Слайды 2 и 3
  const slides = [
    {
      src: `slide-2`,
      p: ` // Монтаж технического этажа, завершение строительства`
    },
    {
      src: `slide-3`,
      p: ` // Финальные работы по лестницам`
    },
  ];

  function getSlideImgSrc(string) {
    return string.replace(`img/object/`, ``).replace(`.jpg`, ``);
  }

  function createSlide(evt, img, p) {
    const imgSrc = `img/popup/` + img + `@1x.jpg`;
    const imgSrcset = `img/popup/` + img + `@2x.jpg`;
    const img320Src = `img/popup/` + img + `-320@1x.jpg`;
    const img320Srcset = `img/popup/` + img + `-320@2x.jpg`;
    const text = evt.target.parentNode.querySelector(`span`).textContent + p;
    return document.createRange().createContextualFragment(`<div class="photo-popup__slide swiper-slide"><picture><source media="(max-width: 767px)" srcset="` + img320Src + ` 1x, ` + img320Srcset + ` 2x"><img src=` + imgSrc + ` srcset=` + imgSrcset + ` width="978" height="615"></picture><p>` + text + `</p></div>`);
  }

  function createPopup(evt) {
    const popupTemplate = document.createRange().createContextualFragment(`<div class="photo-popup js-photo-popup"><div class="photo-popup__wrapper"></div></div>`);
    const closeButton = document.createRange().createContextualFragment(`<button class="photo-popup__close js-popup-close" type="button" atia-label="Закрыть фото"></button>`);
    const constrolsButton = [
      document.createRange().createContextualFragment(`<button class="photo-popup__button photo-popup__button--prev" type="button"><svg width="12" height="20" aria-hidden="true"><use xlink:href="img/sprite.svg#arrow-right"></use></svg></button>`),
      document.createRange().createContextualFragment(`<button class="photo-popup__button photo-popup__button--next" type="button"><svg width="12" height="20" aria-hidden="true"><use xlink:href="img/sprite.svg#arrow-right"></use></svg></button>`),
    ];
    const swiperContainer = document.createRange().createContextualFragment(`<div class="photo-popup__slider swiper-container"><div class="swiper-wrapper"></div></div>`);
    document.body.appendChild(popupTemplate);

    const photoPopup = document.querySelector(`.js-photo-popup`);
    const popupWrapper = photoPopup.querySelector(`.photo-popup__wrapper`);
    popupWrapper.appendChild(closeButton);
    constrolsButton.forEach((button) => {
      popupWrapper.appendChild(button);
    });
    popupWrapper.appendChild(swiperContainer);

    const popupSlider = photoPopup.querySelector(`.swiper-container`);
    const popupSliderWrapper = photoPopup.querySelector(`.swiper-wrapper`);

    // Создание первого слайда на основе выбранного слайда
    let firstSlideSrc = getSlideImgSrc(evt.target.parentNode.querySelector(`img`).dataset.src);
    popupSliderWrapper.appendChild(createSlide(evt, firstSlideSrc, ``));
    // Создание последующих слайдов
    slides.forEach((slide) => {
      popupSliderWrapper.appendChild(createSlide(evt, slide.src, slide.p));
    });
    // Событие на закрытие попапа
    photoPopup.addEventListener(`click`, onPopupClickClose);
    // Инициализация слайдера
    return new Swiper(popupSlider, {
      slidesPerView: 1,
      effect: `fade`,
      fadeEffect: {
        crossFade: true
      },
      navigation: {
        nextEl: `.photo-popup__button--next`,
        prevEl: `.photo-popup__button--prev`,
      },
    });
  }

  function removePopup() {
    const removedPopup = document.querySelector(`.js-photo-popup`);
    removedPopup.removeEventListener(`click`, onPopupClickClose);
    removedPopup.remove();
  }

  function closePopup() {
    layout.classList.remove(`popup-opened`);
    removePopup();
  }

  function openPopup(evt) {
    layout.classList.add(`popup-opened`);
    createPopup(evt);
  }

  function onSlideClick(evt) {
    if (evt.target.parentNode.querySelector(`img`).dataset.action === 'popup') {
      openPopup(evt);
    } else {
      window.location.href = '/contacts.html';
    }

  }

  function onPopupClickClose(evt) {
    if (evt.target.classList.contains(`js-photo-popup`) || evt.target.classList.contains(`js-popup-close`)) {
      closePopup();
    }
  }

  function onWindowEscPress(evt) {
    if (evt.keyCode === 27 && document.querySelector(`.js-photo-popup`)) {
      closePopup();
    }
  }

  sliderContainerList.forEach((slide) => {
    slide.addEventListener(`click`, onSlideClick);
  });


  window.addEventListener(`keydown`, onWindowEscPress);
};
